.spinner-container {
 width: 150px;
 height: 150px;
 position: relative;
 margin: 30px auto;
 overflow: hidden;
}

.spinner {
 position: absolute;
 width: calc(100% - 9.9px);
 height: calc(100% - 9.9px);
 border: 5px solid transparent;
 border-radius: 50%;
 border-top-color: #fff;
 -webkit-animation: spin 5s cubic-bezier(0.17, 0.49, 0.96, 0.79) infinite;
         animation: spin 5s cubic-bezier(0.17, 0.49, 0.96, 0.79) infinite;
}

@-webkit-keyframes spin {
 from {
   -webkit-transform: rotate(0deg);
           transform: rotate(0deg);
 }

 to {
   -webkit-transform: rotate(360deg);
           transform: rotate(360deg);
 }
}

@keyframes spin {
 from {
   -webkit-transform: rotate(0deg);
           transform: rotate(0deg);
 }

 to {
   -webkit-transform: rotate(360deg);
           transform: rotate(360deg);
 }
}

.card {
 position: relative;
 width: 250px;
 height: 120px;
 background: #191414;
 border-radius: 10px;
 padding: 10px;
}

.top {
 position: relative;
 width: 100%;
 display: flex;
 grid-gap: 10px;
 gap: 10px;
}

.pfp {
 position: relative;
 top: 5px;
 left: 5px;
 height: 40px;
 width: 40px;
 background-color: #d2d2d2;
 border-radius: 5px;
 display: flex;
 justify-content: center;
 align-items: center;
}

.title-1 {
