 font-weight: bold;
}

.time {
 width: 90%;
 background-color: #5e5e5e;
 height: 6px;
 border-radius: 3px;
 position: absolute;
 left: 5%;
 bottom: 22px;
}

.elapsed {
 width: 42%;
